(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/jagi:astronomy-relations/lib/module/init_module.js                     //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
var methods = {};                                                                  // 1
                                                                                   // 2
methods.setRelated = function(relationName, value) {};                             // 3
                                                                                   // 4
methods.getRelated = function(relationName) {                                      // 5
  // If there is already a reference to the relation object(s) stored in the       // 6
  // "_references" object then we can take it without looking in collection.       // 7
  if (_.has(this._references, relationName)) {                                     // 8
    return this._references[relationName];                                         // 9
  }                                                                                // 10
                                                                                   // 11
  // Look for the relation definition in this and parent classes.                  // 12
  var relation = Astro.utils.class.findInClass(this.constructor, function(Class) { // 13
    return Class.getRelation(relationName);                                        // 14
  });                                                                              // 15
  if (!relation) {                                                                 // 16
    return;                                                                        // 17
  }                                                                                // 18
                                                                                   // 19
  // Get a collection defined in the relation.                                     // 20
  var ForeignClass = Astro.classes[relation.class];                                // 21
  var ForeignCollection = ForeignClass.getCollection();                            // 22
                                                                                   // 23
  // Prepare selector to select only these documents that much the relation.       // 24
  var selector = {};                                                               // 25
  selector[relation.foreign] = this.get(relation.local);                           // 26
                                                                                   // 27
  // Get a related object.                                                         // 28
  var related;                                                                     // 29
  if (relation.type === 'one') {                                                   // 30
    related = ForeignCollection.findOne(selector);                                 // 31
  } else if (relation.type === 'many') {                                           // 32
    related = ForeignCollection.find(selector);                                    // 33
  }                                                                                // 34
                                                                                   // 35
  // Assing the related object to the "_references" object for further use.        // 36
  return this._references[relationName] = related;                                 // 37
};                                                                                 // 38
                                                                                   // 39
relationsOnInitModule = function() {                                               // 40
  _.extend(Astro.BaseClass.prototype, methods);                                    // 41
};                                                                                 // 42
                                                                                   // 43
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/jagi:astronomy-relations/lib/module/init_class.js                      //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
var checks = {};                                                                   // 1
                                                                                   // 2
checks.relationName = function(relationName) {                                     // 3
  // Check if the field name had been provided and is a string.                    // 4
  if (!_.isString(relationName)) {                                                 // 5
    throw new Error(                                                               // 6
      'The relation name in the "' + this.getName() +                              // 7
      '" class schema has to be a string'                                          // 8
    );                                                                             // 9
  }                                                                                // 10
};                                                                                 // 11
                                                                                   // 12
checks.definitions = function(relationsDefinition) {                               // 13
  if (!_.isObject(relationsDefinition)) {                                          // 14
    // Relations definition has to be an object.                                   // 15
    throw new Error(                                                               // 16
      'The relations definition in the "' + this.getName() +                       // 17
      '" class schema has to be an object'                                         // 18
    );                                                                             // 19
  }                                                                                // 20
};                                                                                 // 21
                                                                                   // 22
checks.definition = function(relationName, relationDefinition) {                   // 23
  if (!_.isObject(relationDefinition)) {                                           // 24
    // Relation definition has to be an object.                                    // 25
    throw new Error(                                                               // 26
      'The relation definition for the "' + relationName +                         // 27
      '" relation in the "' + this.getName() +                                     // 28
      '" class schema has to be an object'                                         // 29
    );                                                                             // 30
  }                                                                                // 31
                                                                                   // 32
  if (relationDefinition.type !== 'one' && relationDefinition.type !== 'many') {   // 33
    // Relation type should be one of two values: "one" or "many".                 // 34
    throw new Error(                                                               // 35
      'The relation type for the "' + relationName +                               // 36
      '" relation in the "' + this.getName() +                                     // 37
      '" class schema should be "one" or "many"'                                   // 38
    );                                                                             // 39
  }                                                                                // 40
};                                                                                 // 41
                                                                                   // 42
var methods = {};                                                                  // 43
                                                                                   // 44
methods.hasRelation = function(relationName) {                                     // 45
  checks.relationName.call(this, relationName);                                    // 46
                                                                                   // 47
  return _.has(this.schema.relations, relationName);                               // 48
};                                                                                 // 49
                                                                                   // 50
methods.getRelation = function(relationName) {                                     // 51
  checks.relationName.call(this, relationName);                                    // 52
                                                                                   // 53
  return this.schema.relations[relationName];                                      // 54
};                                                                                 // 55
                                                                                   // 56
methods.getRelations = function() {                                                // 57
  return this.schema.relations;                                                    // 58
};                                                                                 // 59
                                                                                   // 60
methods.addRelations = function(relationsDefinition) {                             // 61
  checks.definitions.call(this, relationsDefinition);                              // 62
                                                                                   // 63
  _.each(relationsDefinition, function(relationDefinition, relationName) {         // 64
    this.addRelation(relationName, relationsDefinition[relationName]);             // 65
  }, this);                                                                        // 66
};                                                                                 // 67
                                                                                   // 68
                                                                                   // 69
methods.addRelation = function(relationName, relationDefinition) {                 // 70
  // Do params checking. The params checking can throw an error.                   // 71
  checks.relationName.call(this, relationName);                                    // 72
  // If the relation already exists then just break. When adding relation, we also try to add an opposit relation to the foreign schema. It could cause circular method execution. It's why we have to stop execution.
  if (this.hasRelation(relationName)) {                                            // 74
    return;                                                                        // 75
  }                                                                                // 76
  checks.definition.call(this, relationName, relationDefinition);                  // 77
                                                                                   // 78
  // Define setter and getter for the relation.                                    // 79
  Object.defineProperty(this.prototype, relationName, {                            // 80
    get: function() {                                                              // 81
      return this.getRelated(relationName);                                        // 82
    },                                                                             // 83
    set: function(value) {                                                         // 84
      this.setRelated(relationName, value);                                        // 85
    }                                                                              // 86
  });                                                                              // 87
                                                                                   // 88
  // Add relation definition to the schema.                                        // 89
  this.schema.relations[relationName] = relationDefinition;                        // 90
};                                                                                 // 91
                                                                                   // 92
relationsOnInitClass = function(schemaDefinition) {                                // 93
  var Class = this;                                                                // 94
                                                                                   // 95
  _.extend(Class, methods);                                                        // 96
                                                                                   // 97
  // Add the "relations" attribute to the schema.                                  // 98
  Class.schema.relations = {};                                                     // 99
                                                                                   // 100
  if (_.has(schemaDefinition, 'relations')) {                                      // 101
    Class.addRelations(schemaDefinition.relations);                                // 102
  }                                                                                // 103
};                                                                                 // 104
                                                                                   // 105
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/jagi:astronomy-relations/lib/module/init_instance.js                   //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
relationsOnInitInstance = function() {                                             // 1
  // Object for storing references of related objects.                             // 2
  this._references = {};                                                           // 3
};                                                                                 // 4
                                                                                   // 5
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

/////////////////////////////////////////////////////////////////////////////////////
//                                                                                 //
// packages/jagi:astronomy-relations/lib/module/module.js                          //
//                                                                                 //
/////////////////////////////////////////////////////////////////////////////////////
                                                                                   //
Astro.createModule({                                                               // 1
  name: 'relations',                                                               // 2
  init: relationsOnInitModule,                                                     // 3
  events: {                                                                        // 4
    initclass: relationsOnInitClass,                                               // 5
    initinstance: relationsOnInitInstance                                          // 6
  }                                                                                // 7
});                                                                                // 8
                                                                                   // 9
/////////////////////////////////////////////////////////////////////////////////////

}).call(this);
